import Home from './screens/Home';
import AnimalDetail from './screens/AnimalDetail';
import Login from './screens/Login';
import AnimaisDisponiveis from './screens/AnimalsList';
import AddAnimalPage from './screens/AddAnimalPage';
import AdicionarUsuario from './screens/AdicionarUsuario';

import { BrowserRouter as Router, Route } from 'react-router-dom';

const App = () => (
  <Router>
    <div>
      <Route path="/" exact component={Home} />
      <Route path="/Home" exact component={Home} />
      <Route path="/animais/:id" component={AnimalDetail} /> {/* Rota para detalhes do animal */}
      <Route path="/Login" exact component={Login} />
      <Route path="/AnimaisDisponiveis" exact component={AnimaisDisponiveis} />
      <Route path="/AdicionarAnimal" exact component={AddAnimalPage} />
      <Route path="/AdicionarUsuario" exact component={AdicionarUsuario} />

    </div>
  </Router>
);

export default App;
