import React, { useEffect, useState } from 'react'; 
import axios from 'axios';
import '../styles/AnimalListPage.css';
import Footer from '../components/Footer';


const AnimalListPage = () => {
    const [animals, setAnimals] = useState([]);
    const [speciesFilter, setSpeciesFilter] = useState('');
    const [ageFilter, setAgeFilter] = useState('');
    const [sexFilter, setSexFilter] = useState('');
    const [sizeFilter, setSizeFilter] = useState('');
    const [breedFilter, setBreedFilter] = useState('');
    const [cityFilter, setCityFilter] = useState('');
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchAnimals = async () => {
            try {
                const response = await axios.get('http://localhost:3001/animais'); // A API deve incluir a cidade do usuário
                const animalsData = await Promise.all(response.data.map(async (animal) => {
                    const usuarioResponse = await axios.get(`http://localhost:3001/usuarios/${animal.idUsuario}`); // Buscando os detalhes do usuário
                    return { ...animal, usuario: usuarioResponse.data }; // Adiciona os detalhes do usuário ao animal
                }));
                setAnimals(animalsData); // Atualiza o estado com os dados dos animais, agora incluindo o usuário
                setLoading(false);
            } catch (error) {
                console.error('Erro ao buscar os animais:', error);
                setLoading(false);
            }
        };

        fetchAnimals();
    }, []);

    const handleSpeciesFilterChange = (event) => {
        setSpeciesFilter(event.target.value);
    };

    const handleAgeFilterChange = (event) => {
        setAgeFilter(event.target.value);
    };

    const handleSexFilterChange = (event) => {
        setSexFilter(event.target.value);
    };

    const handleSizeFilterChange = (event) => {
        setSizeFilter(event.target.value);
    };

    const handleBreedFilterChange = (event) => {
        setBreedFilter(event.target.value);
    };

    const handleCityFilterChange = (event) => {
        setCityFilter(event.target.value);
    };

    // Cria um conjunto único de raças para o filtro
    const uniqueBreeds = [...new Set(animals.map(animal => animal.raca))];

    // Cria um conjunto único de cidades para o filtro, verificando se o objeto usuario está definido
    const uniqueCities = [...new Set(animals
        .map(animal => animal.usuario ? animal.usuario.cidade : '') // Verifica se o usuario existe
        .filter(city => city))];

    const filteredAnimals = animals.filter(animal => {
        const matchesSpecies = speciesFilter ? animal.especie.toLowerCase() === speciesFilter.toLowerCase() : true;
        const matchesAge = ageFilter ? (ageFilter === 'filhote' ? animal.idade < 1 : animal.idade >= 1) : true;
        const matchesSex = sexFilter ? animal.sexo.toLowerCase() === sexFilter.toLowerCase() : true;
        const matchesSize = sizeFilter ? animal.porte.toLowerCase() === sizeFilter.toLowerCase() : true;
        const matchesBreed = breedFilter ? animal.raca.toLowerCase() === breedFilter.toLowerCase() : true;
        const matchesCity = cityFilter ? (animal.usuario && animal.usuario.cidade.toLowerCase() === cityFilter.toLowerCase()) : true; // Acesse a cidade do usuário

        return matchesSpecies && matchesAge && matchesSex && matchesSize && matchesBreed && matchesCity;
    });

    if (loading) {
        return <div>Carregando...</div>;
    }

    return (
        <div className="animal-list-container">
            <h1>Lista de Animais</h1>
            <div className="filter-container">
                <label htmlFor="species">Filtrar por Espécie:</label>
                <select id="species" value={speciesFilter} onChange={handleSpeciesFilterChange}>
                    <option value="">Todas</option>
                    <option value="Cachorro">Cão</option>
                    <option value="Gato">Gato</option>
                </select>

                <label htmlFor="age">Filtrar por Idade:</label>
                <select id="age" value={ageFilter} onChange={handleAgeFilterChange}>
                    <option value="">Todas</option>
                    <option value="filhote">Filhote</option>
                    <option value="adulto">Adulto</option>
                </select>

                <label htmlFor="sex">Filtrar por Sexo:</label>
                <select id="sex" value={sexFilter} onChange={handleSexFilterChange}>
                    <option value="">Todos</option>
                    <option value="macho">Macho</option>
                    <option value="fêmea">Fêmea</option>
                </select>

                <label htmlFor="size">Filtrar por Porte:</label>
                <select id="size" value={sizeFilter} onChange={handleSizeFilterChange}>
                    <option value="">Todos</option>
                    <option value="pequeno">Pequeno</option>
                    <option value="médio">Médio</option>
                    <option value="grande">Grande</option>
                </select>

                <label htmlFor="breed">Filtrar por Raça:</label>
                <select id="breed" value={breedFilter} onChange={handleBreedFilterChange}>
                    <option value="">Todas</option>
                    {uniqueBreeds.map((breed, index) => (
                        <option key={index} value={breed}>{breed}</option>
                    ))}
                </select>

                <label htmlFor="city">Filtrar por Cidade:</label>
                <select id="city" value={cityFilter} onChange={handleCityFilterChange}>
                    <option value="">Todas</option>
                    {uniqueCities.map((city, index) => (
                        <option key={index} value={city}>{city}</option>
                    ))}
                </select>
            </div>

            <div className="animal-cards">
                {filteredAnimals.map(animal => (
                    <div key={animal.id} className="animal-card">
                        {animal.imagem && <img src={animal.imagem} alt={animal.nome} className='image' />}
                        <h3>{animal.nome}</h3>
                        <p><strong>Espécie:</strong> {animal.especie}</p>
                        <p><strong>Raça:</strong> {animal.raca}</p>
                        <p><strong>Idade:</strong> {animal.idade} anos</p>
                        <p><strong>Sexo:</strong> {animal.sexo}</p>
                        <p><strong>Porte:</strong> {animal.porte}</p>
                        <p><strong>Cidade:</strong> {animal.usuario ? animal.usuario.cidade : 'Desconhecida'}</p> {/* Verifica se usuario existe */}
                        <a href={`/animais/${animal.id}`} className="view-button">Ver Detalhes</a>
                    </div>
                ))}
            </div>
            <Footer />
        </div>

    );
};

export default AnimalListPage;
